import React from "react";
import classes from "./customFields.module.css"
import {Field} from "redux-form";

export const Textarea = ({input, meta, ...restProps}) => {
    const isError = meta.touched && meta.error; //     meta    
    return (
        <div>
            <div >
                <textarea {...input} //    redux field validation
                          placeholder={restProps.placeholder} //    
                          className={isError?classes.textAreaError:classes.textArea}
                    // textarea      meta
                />
            </div>
            <div >
                {isError && <span className={classes.textError}>{meta.error}</span>}
{/*                   meta   ,    */}
            </div>
        </div>
    )
}
export const Input = ({input, meta, ...restProps}) => {
    const isError = meta.touched && meta.error; //     meta    
    return (
        <span>
            <span >
                <input {...input} //    redux field validation
                          placeholder={restProps.placeholder} //    
                          className={isError?classes.InputError:classes.Input}
                    // login      meta
                />
            </span>
            <div >
                {isError && <span className={classes.textError}>{meta.error}</span>}
{/*                   meta   ,    */}
            </div>
        </span>
    )
}

export const CreateField = (name, component, placeholder, validate ) => {
    return (<div>
        <Field
            name={name}//          
            component={component}//  -    
            placeholder={placeholder}//     
            validate={validate}
        />
    </div>)

}



