import React from 'react'; //  
import classes from './MyPosts.module.css' // css 
import Post from "./Post/Post"; //     map
import {Field, reduxForm, reset} from "redux-form";
import {maxLengthCreator, Required} from "../../common/Validation/validationField";
import {Textarea} from "../../common/Validation/customFields";
import {bedug_mode} from "../../../redux/store-redux"; // reduxForm    

const newPostForm = ({handleSubmit}) => { //  
    return (
        <form onSubmit={handleSubmit} /*      reduxForm - handleSubmit*/>
            <div>
                <div className={classes.newPostFormTextArea}>
                    <Field
                        name={"newPostData"} //          
                        component={Textarea} //       
                        placeholder={"newPost"} //     
                        validate={[Required, maxLengthCreator(60)]} //       
                    />
                </div>
                <div>
                    <button>Submit</button>
                    {/**/}
                </div>
            </div>
        </form>
    )
}

//   ,    "newPostForm"
const NewPostReduxForm = reduxForm({form: "newPostForm"})(newPostForm)

const MyPosts = ({userId, state, addPost, dispatch}) => { //    
    if (bedug_mode) {
        console.log("MyPosts.jsx")
    } // 

    let postElements = state.posts.map((p) => //      map
        <Post key={p.id} message={p.message} like={p.like} id={p.id}/>);

    let AddPost = (formData) => { //        
//   
        dispatch(reset('newPostForm'))
        addPost(formData.newPostData);
    };

    return (
        <div className={classes.postsBlock} /**/ >
            {(userId === 0) && //       (   ID  profile)
                <div>
                    <h3>My posts</h3> {/*h3 */}
                    <NewPostReduxForm
                        onSubmit={AddPost}/> {/*        */}
                    <div className={classes.posts}>
                        {postElements} {/* */}
                    </div>
                </div>
            }
        </div>
    )
}
export default MyPosts;
//   ,     
