import React, {useEffect} from "react"; //  
import {Field, initialize, reduxForm} from "redux-form";// reduxForm     reset    
import classes from './EditProfile.module.css'
import styles from './../../../common/Validation/customFields.module.css'
import {Input, CreateField} from "../../../common/Validation/customFields";

const ProfileForm = ({setEditMode, error, handleSubmit, reset, profile}) => { //  
    let ContactCreateField = (key1) => {
        return <div>
            {CreateField(key1, Input, key1, [] )} {/*name, component, placeholder, validate*/}
        </div>
    }
    return (
        <form onSubmit={handleSubmit}/*      reduxForm - handleSubmit*/>
            <span>
                <div>
                    {CreateField("FullName", Input, " 1", [] )} {/*name, component, placeholder, validate*/}
                </div>
                <div>
                    {CreateField("AboutMe", Input, " 1", [] )} {/*name, component, placeholder, validate*/}
                </div>
                <div>
                    <label>  :</label>
                    <Field
                        name="LookingForAJob"//          
                        component="input"//  - 
                        type="checkbox"//  -    ? (true/false)
                        validate={[]}
                    />
                </div>
                <div>
                    {CreateField("LookingForAJobDescription", Input, "  1", [] )} {/*name, component, placeholder, validate*/}
                </div>
                <label></label>
                <div className={classes.EditProfileContactsFields}>
                    <div>
                        {Object.keys(profile.contacts).map((key1)=>{
                            return <span key={key1}>{ContactCreateField(key1)}</span>
                        })}
                    </div>
                </div>
                <button type="button" onClick={reset}>  </button>

                <span>
                    <button type="submit"></button>
                    {/**/}
                </span>
                <span>
                    <button type="button" onClick={()=>{setEditMode(false)}}></button>
                    {/**/}
                </span>
                <span className={styles.commonError}>
                    {/*          */}
                    {error} {/*     stopSubmit  auth-reducer.jsx */}
                </span>
            </span>
        </form>
    )
}

//   ,    "EditProfileForm"
let EditProfileReduxForm = reduxForm({form: 'EditProfileForm'})(ProfileForm)

let EditProfile = ({putProfile, dispatch, setEditMode, profile, userId, myId}) => {
    let onSubmit = (formData) => { //          formData
        //      dispatch(reset('EditProfileForm')) //     
        const LookingForAJob = !formData.LookingForAJob ? false : formData.LookingForAJob //   LookingForAJob  ,  false
        putProfile(
            formData.FullName,
            formData.AboutMe,
            LookingForAJob,
            formData.LookingForAJobDescription,
            formData.github,
            formData.vk,
            formData.facebook,
            formData.instagram,
            formData.twitter,
            formData.website,
            formData.youtube,
            formData.mainLink,
        )// putMyProfileThunkCreator   ProfileContainer
        setEditMode(false)
    }
/*
    let resetFormFields = () => { //    ,    
        dispatch(reset('EditProfileForm')) //     
    }
*/

    let initialProfile = {
        FullName: profile.fullName,
        AboutMe: profile.aboutMe,
        LookingForAJob: profile.lookingForAJob,
        LookingForAJobDescription: profile.lookingForAJobDescription,
        github: profile.contacts.github,
        vk: profile.contacts.vk,
        facebook: profile.contacts.facebook,
        instagram: profile.contacts.instagram,
        twitter: profile.contacts.twitter,
        website: profile.contacts.website,
        youtube: profile.contacts.youtube,
        mainLink: profile.contacts.mainLink,
    }

    useEffect(()=>{
        dispatch(initialize("EditProfileForm", initialProfile)); //     
        if (userId !== 0) {setEditMode(false)} //        ,  editMode
    })
    return (
        <div>
            <div className={classes.HeaderEditProfileForm}> </div>
            {/*h3 */}
            <div>(  )</div>
            <div className={classes.EditProfile}>
                <div>
                    <EditProfileReduxForm
                        onSubmit={onSubmit}
                        setEditMode = {setEditMode}
                        profile = {profile}
                    />{/*        */}
                </div>
            </div>
        </div>

    )
}

export default EditProfile
